
@media screen and (min-width:600px) {
.photoIndex {
    width: 100%;
    height: 92vh;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    text-align: center;
    padding: 40px 22%;
    box-sizing: border-box;
}
.description {
    text-align: left;
    margin-bottom: 30px;
}
.photoIndex > img,
  .photoIndex > .video-wrap {
    width: 100%;
    margin: 0 auto 10px;
}
}
@media screen and (max-width:600px) {
.photoIndex {
    width: 100%;
    height: 92vh;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    text-align: center;
    padding: 20px 10%;
    box-sizing: border-box;
}
.description {
    text-align: left;
    margin-bottom: 20px;
}
.description > h3 {
    margin: 0;
}
.photoIndex > img,
  .photoIndex > .video-wrap {
    width: 100%;
    margin: 0 auto 10px;
}
}



